@import '@/assets/sass/variables';

$C2C-orange: red;

:deep(.elevation-profile-chart) {
  height: 150px; // default height on page

  .axis {
    pointer-events: none;
  }

  .axis path,
  .axis line {
    fill: none;
    stroke: $grey;
    shape-rendering: crispEdges;
  }

  .axis .legend {
    fill: $grey;
  }

  .axis .tick {
    color: $grey;
  }

  .axis.x .tick line {
    display: none;
  }

  .line {
    fill: none;
    stroke: $C2C-orange;
    stroke-width: 3px;
    stroke-linejoin: round;
  }

  .circle {
    fill: yellow;
    stroke: $C2C-orange;
    stroke-width: 2px;
  }

  .popup {
    font-size: 12px;
    font-weight: bold;
    fill: $primary;
  }

  .popup text {
    fill: yellow;
  }

  .overlay {
    fill: none;
    pointer-events: all;
  }

  .area {
    fill: none;
  }

  .area-highlighted {
    fill: rgba(255, 255, 0, 0.75);
  }

  .areas-lines {
    stroke: $grey;
    stroke-width: 1px;
    stroke-dasharray: 5;
  }
}

.elevation-generate-panel {
  margin-top: 1rem;
  margin-left: -0.75rem;
  margin-right: -0.75rem;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  border-top: 1px solid lightgray;
}

.input-interpolate {
  width: 100px;
  margin-right: 0.75rem;
}

.control-subpanel {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  line-height: 2;
  margin-left: 0.75rem;
  margin-bottom: 0.75rem;
  padding-left: 2rem;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  border-left: solid 1px $primary;
}
