
          @import '@/assets/sass/variables.scss';
          @import '@/assets/sass/mixins.scss';
        

@import '@/assets/sass/variables';

.info-text {
  font-size: 0.85em;
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
}
.is-info {
  color: $primary;
}
.is-warning {
  color: $red;
}
.is-help {
  background: $grey-lightest;
  color: $grey-dark;
  border-radius: 4px;
  padding: 0.75rem;
  margin-bottom: 1.5rem !important;
}
.info-icon {
  float: left;
}
.info-content {
  margin-left: 2rem;
}

.is-inline-block {
  &.info-text {
    margin-top: 0;
    margin-bottom: 0;
  }
  .info-icon {
    float: none;
  }
  .info-content {
    margin-left: 0;
  }
}
