@import '@/assets/sass/variables';

.overlay {
  min-width: 150px;
  max-width: 100%;
  background: $white;
  border-radius: 4px;
  box-shadow: 0 0 3px rgba($black, 0.5), 0 3px 8px rgba($black, 0.5);
  overflow: hidden;
}
.overlay-header {
  font-size: 0.9em;
  padding: 5px 0;
  text-align: center;
  color: $white;
}
.overlay-content {
  text-align: center;
  padding: 5px;
  vertical-align: 1px;
}
.overlay-content a:hover,
.overlay-content a:focus {
  text-decoration: underline;
}
.overlay-link-icon {
  margin-left: 5px;
}
.overlay-icon {
  position: absolute;
  transform: scale(1.3);
  top: -4px;
  left: -6px; /* iconSize / 2 */
  margin: auto;
  z-index: 2;
  pointer-events: none;
}
